import { defineStore } from 'pinia'
import api from '@/service'
const useCityStore = defineStore('city', {
  state: () => ({
    allCity: null,
    selectCity: { cityName: '广州' }
  }),
  actions: {
    async geiCityAll() {
      const res = await api.getCityAll()
      // console.log(res)
      this.allCity = res.data
    }
  }
})

export default useCityStore
